import React from 'react';
import { extname, basename } from 'path';
import mimeTypes from 'mime-types';
import { FormattedMessage } from 'react-intl';

import text from './../style/img/text.png';
import msword from './../style/img/msword.png';
import msexcel from './../style/img/msexcel.png';
import mspowerpoint from './../style/img/mspowerpoint.png';
import * as  constants from '../constants';

import styles from './FileInfo.module.scss';

const imgForFileType = (type) => {
  switch (constants.allowedUploadTypes[type]) {
    case 'msword':
      return msword;
    case 'msexcel':
      return msexcel;
    case 'mspowerpoint':
      return mspowerpoint;
    default:
      return text;
  }
};

const FileInfo = ({ path, words }) => {
  const ext = extname(path);

  const img = imgForFileType(mimeTypes.lookup(ext));
  const name = basename(path);

  return (
    <div className={styles.holder}>
      <div className={styles.Image}><img width={100} src={img} alt={ext}/></div>
      <div className={styles.Name}>{name}</div>
      <div className={styles.WordCount}><FormattedMessage
        id="Order.Words"
        defaultMessage={'{words, number} {words, plural, one {word} other {words}}'}
        values={{ words }}
      /></div>
    </div>);
};

export default FileInfo;
