import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Slider from 'rc-slider';
import './TimePickerField.scss';
import './rc-slider.scss';

class TimePickerField extends Component {

  state = {
    sliderActive: false,
  };

  static propTypes = {
    input: PropTypes.object.isRequired,
    marks: PropTypes.object,
    max: PropTypes.number,
    meta: PropTypes.object,
    tooltip: PropTypes.object,
    onBeforeChange: PropTypes.func,
    onAfterChange: PropTypes.func,
  };

  static defaultProps = {
    onBeforeChange: () => {},
    onAfterChange: () => {},
  };

  render = () => {
    const { input, marks, max, meta: { warning } } = this.props;
    const { sliderActive } = this.state;

    if (!input.value) input.value = 0;
    return (
      <div className={`section-slider ${sliderActive ? 'section-slider--active' : ''}`}>
        <div className="section-slider__left"/>
        <div className="section-slider__slider">
          <Slider
            marks={marks}
            max={max}
            included={false}
            onBeforeChange={this.onBeforeChange}
            onAfterChange={this.onAfterChange}
            {...input}
          />
          {warning}
        </div>
        <div className="section-slider__right"/>
      </div>
    );
  };

  onBeforeChange = (...args) => {
    this.props.onBeforeChange.apply(this, args);

    this.setState({ sliderActive: true });
  };

  onAfterChange = (...args) => {
    this.props.onAfterChange.apply(this, args);

    this.setState({ sliderActive: false });
  }
}

export default TimePickerField;
